.scroll-up-btn-icon {
  cursor: pointer;
  stroke: var(--body-secondary-color);
  stroke-width: 1px;
  background: radial-gradient( 183.6% 183.01% at 59.76% -23.42%, var(--text-fourth-color) 18.03%, var(--body-secondary-color) 100% );
  border-radius: 50%;
  transition: transform .25s linear, stroke .25s linear;
  animation: pulse-animation 8s infinite;
}

@keyframes pulse-animation {
  0% {
    transform: scale(1);
  }

  25% {
    transform: scale(1.2);
  }

  35% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  65% {
    transform: scale(1);
  }

  75% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

.js-scroll-up .scroll-up-btn-icon:hover, .js-scroll-up .scroll-up-btn-icon:focus {
  stroke: rgba(79, 46, 232, .7);
  animation-play-state: paused;
}

.js-scroll-up {
  opacity: 1;
  transition: opacity .25s linear;
}

.js-scroll-up_hidden {
  opacity: 0;
}



.loader {
  width: 200px;
  height: 140px;
  box-sizing: border-box;
  perspective: 1000px;
  background: #979794;
  border-radius: 8px;
  position: relative;
}

.loader:before {
  content: "";
  background-color: #f5f5f5;
  background-image: linear-gradient(#ddd 100px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 100px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 100px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 100px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 100px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 100px, rgba(0, 0, 0, 0) 0);
  background-position: 15px 30px, 15px 60px, 15px 90px, 105px 30px, 105px 60px, 105px 90px;
  background-repeat: no-repeat;
  background-size: 60px 10px;
  background-attachment: scroll;
  background-origin: padding-box;
  background-clip: border-box;
  border-radius: 8px;
  position: absolute;
  top: 10px;
  bottom: 10px;
  left: 10px;
  right: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .25);
}

.loader:after {
  content: "";
  width: calc(50% - 10px);
  transform-origin: 0;
  background-color: #fff;
  background-image: linear-gradient(#ddd 100px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 100px, rgba(0, 0, 0, 0) 0), linear-gradient(#ddd 100px, rgba(0, 0, 0, 0) 0);
  background-position: 50% 30px, 50% 60px, 50% 90px;
  background-repeat: no-repeat;
  background-size: 60px 10px;
  background-attachment: scroll;
  background-origin: padding-box;
  background-clip: border-box;
  border-radius: 8px;
  animation: paging 1s linear infinite;
  position: absolute;
  top: 10px;
  bottom: 10px;
  right: 10px;
  transform: rotateY(0deg);
}

@keyframes paging {
  to {
    transform: rotateY(-180deg);
  }
}

